<template>
    <v-navigation-drawer
        color="primaryCard"
        v-model="$root.$children[0].showPlaylists"
        width="325"
        right
        absolute
        temporary
        floating
        overlay-color="black">
            <div class="vuebar-element" v-bar>
                <v-list>
                    <v-list-item>
                        <v-list-item-content>
                            <v-list-item-title class="title pa-5 text-center text-uppercase primaryText--text">Playlists</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>

                    <v-divider class="secondaryCard"></v-divider>

                    <v-list-item class="pa-0">
                        <v-list-item-content class="justify-center" >
                            <v-list-item-group
                                class="my-2 elevation-10"
                                style="width: 90%; height: 125px; border-radius: 4px"
                                v-for="(playlist, index) in playlists"
                                :key="playlist[index]">
                                <v-img
                                    v-if="$root.$children[0].playerStuff.satanic"
                                    style="z-index: 1; border-radius: 4px"
                                    class="cover"
                                    :style="{backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.85)), url(images/satanic/playlist.gif)'}"
                                    ></v-img>
                                <v-img 
                                    v-else
                                    style="z-index: 1; border-radius: 4px"
                                    class="cover"
                                    :style="{backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.85)), url(images/playlists/' + playlist.cover + '.jpg)'}"></v-img>

                                <v-btn
                                    @click="changePlaylist(index)"
                                    :value="playlist.title"
                                    style="z-index: 2"
                                    color="secondaryText"
                                    text
                                    width="100%"
                                    height="100%">{{ playlist.title }}</v-btn>
                            </v-list-item-group>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </div>
    </v-navigation-drawer>
</template>>

<script>

export default {
    name: 'playlists',
    props: ['show-playlists', 'changeplaylist'],
    data () {
        return {
            playlists: [
                { title: '80s Slow Dance Club', playlistId: 'PLvheczCxmsyQBg4Nkn_-T_IkKuEFq1bv9', cover: '80s-slow-dance-club' },
                { title: 'Trance', playlistId: 'PLvheczCxmsyQzMw-6BbS4OBK0nUJeenni', cover: 'trance' },
                { title: 'Bass House', playlistId: 'PLvheczCxmsyTkC_cWvWgfWnTkj5CyeTUr', cover: 'bass-house' },
                { title: 'Emo Rap', playlistId: 'PLvheczCxmsyRcZNmzQYWUkmnhX1s7vEW4', cover: 'emo-rap' },
                { title: 'Chill Rap', playlistId: 'PLvheczCxmsyRo8wtEQOy6ugZzkwG6bNNW', cover: 'chill-rap' },
                { title: 'Angry Rap', playlistId: 'PLvheczCxmsyS73n1kzm-QHKaA-oH8ToZK', cover: 'angry-rap' },
                { title: 'It\'s not a phase, mum', playlistId: 'PLvheczCxmsyRv6iaw_K8SI6awiXOv0Bkc', cover: 'its-not-a-phase-mum' },
                { title: 'Normie Slappers', playlistId: 'PLvheczCxmsyTiGwOZ18MdS8okc_3gTazh', cover: 'normie-slappers'},
            ]
        }
    },
    methods: {
        changePlaylist (index) {
            this.$root.$children[0].currentPlaylist.title = this.playlists[index].title
            this.$root.$children[0].currentPlaylist.cover = this.playlists[index].cover
            this.$root.$children[0].showPlaylists = false
            this.player.loadPlaylist({
                list: this.playlists[index].playlistId,
                listType: 'playlist'
            })
        }
    },
    computed: {
        player () {
            return this.$root.$children[0].player
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/themes.scss';

.cover {
    position: absolute;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    z-index: 1;
}
</style>>