<template>
    <v-dialog
        v-model="$root.$children[0].showSettings"
        overlay-color="black"
        max-width="800px">
        <v-tabs 
            background-color="transparent"
            color="primaryText">
            <v-tab>Themes</v-tab>
        </v-tabs>
        <v-list-item class="pa-0">
                <v-list-item-content class="justify-center">
                    <v-list-item-group
                        class="my-2 elevation-10"
                        style="width: 90%; height: 125px; border-radius: 4px"
                        v-for="(theme, index) in themes"
                        :key="theme[index]">
                        <v-img 
                            v-if="$root.$children[0].playerStuff.satanic"
                            class="cover"
                            :style="{backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url(images/satanic/theme.gif)' }"></v-img>
                        <v-img 
                            v-else
                            class="cover"
                            :style="{backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url(' + theme.thumbnail + ')' }"></v-img>
                        
                        <v-btn
                            @click="changeTheme(index)"
                            :value="theme.title"
                            color="secondaryText"
                            text
                            width="100%"
                            height="100%">{{ theme.title }}</v-btn>
                    </v-list-item-group>
                </v-list-item-content>
            </v-list-item>
        
    </v-dialog>
</template>

<script>
export default {
    name: 'settings',
    props: ['show-settings'],
    data () {
        return {
            themes: [
                { title: 'Purple', themeClass:'purple', thumbnail: 'images/backgrounds/purple.jpg'},
                { title: 'Orange', themeClass:'orange', thumbnail: 'images/backgrounds/orange.jpg'},
                { title: 'Satanic', themeClass:'satanic', thumbnail: 'images/satanic/bg.gif'}
            ]
        }
    },
    methods: {
        changeTheme (index) {
            this.$root.$children[0].activeTheme = this.themes[index].themeClass
        }
    }
}
</script>

<style lang="scss">
@import '../assets/styles/themes.scss';

.cover {
    position: absolute;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    z-index: 1;
}

.v-dialog {
    @include themify($themes) {
        background: themed('primaryCard');  
    }

    .v-btn {
        @include themify($themes) {
            color: themed('primaryText');  
        }
        z-index: 2
    }

    .v-image {
        z-index: 1; 
        border-radius: 4px !important;
    }
}
</style>