<template>
    <v-app :class="'theme-' + activeTheme">
        <v-flex 
            d-flex
            justify-center 
            align-center 
            flex-column >
            <navbar
                @togglesettings="toggleSettings"/>

            <youtube
                style="width: 688px; height: 387px"
                class="elevation-10"
                ref="youtube" 
                :player-vars="playerVars" 
                @playing="playing" 
                @paused="paused"
                @ready="ready" />
        </v-flex>

        <controls 
            :playerStuff="playerStuff" 
            :alerts="alerts"
            :currentPlaylist="currentPlaylist"
            @toggleplaylists="togglePlaylists" />

        <playlists 
            :show-playlists="showPlaylists" />

        <settings 
            :show-settings="showSettings" />


        <v-snackbar
            :style="[ $vuetify.breakpoint.smAndDown ? {'bottom': '0', 'left': '0', 'width': '100vw'} : {'bottom': '1.5rem', 'right': '1.5rem'} ]"
            v-model="alerts.shuffle"
            multi-line
            bottom
            right
            color="secondaryCard thirdText--text"
            :timeout="3000">Playlist Shuffled
            <v-btn @click="alerts.shuffle = false" text large color="primaryText">close</v-btn>
        </v-snackbar>

        <v-snackbar
            :style="[ $vuetify.breakpoint.smAndDown ? {'bottom': '0', 'left': '0', 'width': '100vw'} : {'bottom': '1.5rem', 'right': '1.5rem'} ]"
            v-model="alerts.loop"
            multi-line
            bottom
            right
            color="secondaryCard thirdText--text"
            :timeout="3000">Playlist Looped
            <v-btn @click="alerts.loop = false" text large color="primaryText">close</v-btn>
        </v-snackbar>
            
        <div v-if="playerStuff.satanic" style="height: 100%; width: 100%; position: absolute; z-index: -1">
            <div :style="{'background-image': 'url(images/satanic/bg.gif)'}" id="background"></div>
            <v-flex class="d-flex justify-space-around align-center flex-row" style="height: 100%;">
                <v-avatar
                    class="pentagram"
                    size="500" >
                    <v-img
                        aspect-ratio="1.33"
                        :src="'images/satanic/penta.gif'"
                        size></v-img>
                </v-avatar>
                
                <v-avatar
                    class="pentagram"
                    size="500" >
                    <v-img
                        aspect-ratio="1.33"
                        :src="'images/satanic/penta.gif'"
                        size></v-img>
                </v-avatar>
            </v-flex>
        </div>

        <div v-else :style="{'background-image': 'url(images/backgrounds/' + activeTheme + '.jpg)'}" id="background"></div>
    </v-app>
</template>

<script>
import navbar from './components/navbar.vue';
import controls from './components/controls.vue';
import playlists from './components/playlists.vue';
import settings from './components/settings.vue';

export default {
    name: 'App',

    components: {
        navbar,
        controls,
        playlists,
        settings
    },

    data () {
        return {
            playerVars: {
                list: 'PLvheczCxmsyQBg4Nkn_-T_IkKuEFq1bv9',
                listType: 'playlist',
                mute: 0,
                loop: 0,
                autoplay: 0,
                color: 'white',
                rel: 0,
                controls: 1,
                fs: 0,
                iv_load_policy: 3
            },
            playerStuff: {
                isPlaying: false,
                isLooped: false,
                isShuffled: false,
                volume: 25,
                satanic: false
            },
            alerts: {
                shuffle: false,
                loop: false
            },
            currentPlaylist: {
                title: '80s Slow Dance Club',
                cover: '80s-slow-dance-club'
            },
            showPlaylists: false,
            showSettings: false,
            activeTheme: 'purple'
        }
    },
    methods: {
        playing () {
            this.playerStuff.isPlaying = true
        },
        paused () {
            this.playerStuff.isPlaying = false
        },
        ready () {
            this.player.setVolume(this.volume)
        },
        toggleSettings () {
            this.showSettings = true
        },
        togglePlaylists () {
            this.showPlaylists = true
        }
    },
    computed: {
        player () {
            return this.$refs.youtube.player
        },
        volume () {
            return this.playerStuff.volume
        },
        theme () {
            return this.activeTheme
        }

    },
    async mounted () {
        if (localStorage.volume) {
            this.playerStuff.volume = localStorage.volume
        }
        if (localStorage.theme) {
            this.activeTheme = localStorage.theme
        }
    },
    watch: {
        volume (newVol) {
            localStorage.volume = newVol
            this.player.setVolume(newVol)
        },
        theme (newTheme) {
            localStorage.theme = newTheme
            if (this.activeTheme == 'satanic') {
                this.playerStuff.satanic = true;
            } else {
                this.playerStuff.satanic = false;
            }
        }
    }
};
</script>

<style lang="scss">
@import './assets/styles/themes.scss';

html { overflow-y: auto !important }

.v-application--wrap {
    z-index: 1;
}

#background {
    width: 100%;
    height: 100%;
    position: fixed;
    transform: scale(1.1);
    filter: blur(5px);
    z-index: -1;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
}

.pentagram {
    filter: hue-rotate(125deg)
            blur(2px);
}

.pentagram:last-child {
    transform: rotateY(180deg);
}
</style>