import Vue from 'vue'
import App from './App.vue'
//plugins
import VueYoutube from 'vue-youtube'
import vuetify from './plugins/vuetify'

import Vuebar from 'vuebar'
Vue.use(Vuebar)

Vue.use(VueYoutube)

Vue.config.productionTip = false

window.Event = new Vue();

new Vue({
  vuetify,
  render: h => h(App)
}).$mount('#app')
